.login-screen{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-screen_form{
  width: 300px;
  padding: 1.5rem;
  box-shadow: 0 1rem 2rem rgba(0,0,0,0.2);
  background: #fff;
}
.login-screen_title{
  text-align: center;
  margin-bottom: 1rem;
}

.login-screen_subtext{
  font-size: 0.7rem;
  display: block;
  margin: 0.5rem 0;
}
